import React, { useState } from 'react';
import './LinearTreeView.css'; // Import your custom CSS file for styling

function TreeNode({ label, children , parent}) {
  const [expanded, setExpanded] = useState(false);

  const toggleNode = () => {
    setExpanded(!expanded);
  };

  return (
    <li>
      <div
        className={`linear-tree-node ${expanded ? 'expanded' : ''}`}
        onClick={toggleNode}

      >
        {children && (
          <span className="linear-arrow-icon">{expanded ? '▼' : '▶'}</span>
        )}
        {label.toLowerCase() === parent?.toLowerCase() ?
        <div className="parent-tree-node-content">Drug Product - {label}</div> : <div className="child-tree-node-content">Drug Substance ({label})</div> }
      </div>

      {children && expanded && (

        <ul className="linear-sub-tree">
          {children.map((child, index) => {
            return (
              <>
                <TreeNode
                  key={index}
                  label={child.ComponentItemNumber}
                  children={null} // Set children to null since there are no sub-children for leaf nodes
                />
              </>
            )
          })}
        </ul>
      )}

    </li> 
  );
}

function CustomTreeView(props) {
  const { data, parent } = props;

  return (
    <ul className="linear-tree-view">
      {data && data.map((node) => {
        return (
          <>
            <TreeNode
              key={node.id}
              label={node.CommonItemNumber}
              children={node.Component}
              parent={parent}
            />
          </>
        )
      })}
    </ul>
  );
}

export default CustomTreeView;
